<template>
    <el-row>
        <basic-header :items="['水司管理','分类管理']"></basic-header>
        <el-tabs v-model="activeName">
            <el-tab-pane label="分类查看" name="list">
                <el-tree :data="data" :expand-on-click-node="false">
                    <template #default="o">
                    <span class="custom-tree-node">
                        <span>{{o.node.label}}</span>
                        <span style="float: right">
                            <el-button type="text" size="mini" @click="edit(o.data.id)">编辑</el-button>
                            <el-button type="text" size="mini" @click="del(o)">删除</el-button>
                        </span>
                    </span>
                    </template>
                </el-tree>
            </el-tab-pane>
            <el-tab-pane label="分类添加" name="add">
                <el-col :span="12">
                <el-form :model="form2" label-width="80px" :rules="rules2" ref="addForm">
                    <el-form-item label="上级分类" prop="pid">
                        <el-select v-model="form2.pid">
                            <el-option label="无" :value="0"></el-option>
                            <el-option v-for="item in data" :key="item.id" :label="item.label" :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="分类名称" prop="name">
                        <el-input v-model="form2.name"></el-input>
                    </el-form-item>
                    <el-form-item label="分类排序" prop="sort">
                        <el-input type="number" v-model="form2.sort"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="add">提交</el-button>
                    </el-form-item>
                </el-form>
                </el-col>
            </el-tab-pane>
        </el-tabs>
        <el-dialog
                title="编辑"
                :visible.sync="dialogVisible"
                width="50%">
            <el-form label-width="80" :model="form" :rules="rules" ref="myForm">
                <el-form-item label="分类名称" prop="name">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="分类排序" prop="sort">
                    <el-input type="number" v-model="form.sort"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible=false">取消</el-button>
                <el-button type="primary" @click="submit">确定</el-button>
            </span>
        </el-dialog>
    </el-row>
</template>

<script>
    import BasicHeader from "@/components/BasicHeader"
    export default {
        name: "CategoryManage",
        data: () => ({
            activeName:"list",
            data:[],
            dialogVisible:false,
            form:{
                name:"",
                sort:"",
            },
            rules:{
                name:{required:true,message:"请输入分类名称",trigger:"blur"},
                sort:{required:true,message:"请输入分类排序",trigger:"blur"}
            },
            form2:{
                name:"",
                sort:"",
                pid:""
            },
            rules2:{
                name:{required:true,message:"请输入分类名称",trigger:"blur"},
                sort:{required:true,message:"请输入分类排序",trigger:"blur"},
                pid:{required:true,message:"请选择上级分类",trigger:"change"}
            }
    // {
    //     label:"衣服",
    //         children:[
    //     {
    //         label:"上衣"
    //     },
    //     {
    //         label:"裤子"
    //     }
    // ]
    // },{
    //     label:"鞋子"
    // }
        }),
        methods: {
            fetchData(){
                this.$axios.get(this.$api.CATEGORY).then(res=>{
                    if(res.data.code===200){
                        let data=res.data.data;
                        let arr=[];
                        data.forEach(obj=>{
                            if(obj.pid===0){
                                let o={};
                                o.label=obj.name;
                                o.children=[];
                                o.id=obj.id;
                                o.sort=obj.sort;
                                arr.push(o);
                            }else{
                                let o={};
                                o.label=obj.name;
                                o.id=obj.id;
                                o.sort=obj.sort;
                                let p=arr.find(v=>(v.id===obj.pid));
                                p.children.push(o);
                            }
                        });
                        arr.sort((a,b)=>{
                            if(a.sort<b.sort){
                                return 1;
                            }else{
                                return -1;
                            }
                        })
                        this.data=arr;
                    }
                })
            },
            del(o){
                let msg="";
                if(o.node.level===1){
                    msg="删除当前栏目后，该栏目的子栏目也会被删除，确定吗？";
                }else{
                    msg="删除当前栏目后，该栏目下的商品也会被删除，确定吗";
                }
                this.$confirm(msg, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$axios.delete(this.$api.CATEGORY,{id:o.data.id}).then(res=>{
                        if(res.data.code===200){
                            this.$message.success("删除成功");
                            this.fetchData();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            edit(id){
                this.$axios.get(this.$api.CATEGORY,{id}).then(res=>{
                    if(res.data.code===200){
                        // console.log(res);
                        this.form=res.data.data;
                        this.dialogVisible=true;
                    }
                })
            },
            submit(){
                this.$refs.myForm.validate(valid=>{
                    if(valid){
                        this.$axios.put(this.$api.CATEGORY,this.form).then(res=>{
                            if(res.data.code===200){
                                this.dialogVisible=false;
                                this.$message.success("修改成功");
                                this.fetchData();
                            }
                        })
                    }else{
                        return false;
                    }
                })
            },
            add(){
                this.$refs.addForm.validate(valid=>{
                    if(valid){
                        this.$axios.post(this.$api.CATEGORY,this.form2).then(res=>{
                            if(res.data.code===200){
                                this.$message.success("添加成功");
                                this.activeName="list";
                                this.fetchData();
                            }
                        })
                    }else{
                        return false;
                    }
                })
            }
        },
        components:{
          "basic-header":BasicHeader
        },
        mounted() {
            this.fetchData();
        }
    }
</script>

<style scoped lang="scss">
    .custom-tree-node{
        width: 50%;
    }
</style>